import { mapState, mapMutations } from 'vuex'
export default {
  props: {
    CoinList: {
      type: Array,
      default: []
    },
  },
  data () {
    return {
      value: '所有订单',
      options: [
        {
          label: '所有订单',
          value: '所有订单'
        },
        {
          label: '部分订单',
          value: '部分订单'
        }
      ],
      coinMarketList: [{
        id: '20210836748506116096',
        time: '2021-04-01 15:16:42',
        name: 'BTC',
        alias: 'Bitcoin',
        price: '$1000.00',
        percent: '3.39',
        Quantity: '4,365.01',
        PartyA: '安心快捷  全程无忧',
        status: true
      }, {
        id: '20210836748506116096',
        time: '2021-04-01 15:16:42',
        name: 'BTC',
        alias: 'Bitcoin',
        price: '$1000.00',
        percent: '3.39',
        Quantity: '4,365.01',
        PartyA: '安心快捷  全程无忧',
        status: true
      }, {
        id: '20210836748506116096',
        time: '2021-04-01 15:16:42',
        name: 'BTC',
        alias: 'Bitcoin',
        price: '$1000.00',
        percent: '3.39',
        Quantity: '4,365.01',
        PartyA: '安心快捷  全程无忧',
        status: true
      }],
      pageTotal: 0,
      GetOrderPageParams: {
        "page": 1,// "页码"
        "pageSize": 10,// "单页条数"
        "type": 0,//0全部，1进行中
        "advertCode": "",// "挂单币种"
        "orderType": 0,// "交易类型:1购买,2出售"
        "status": 0,// "状态:1未付款,2已付款,3已取消,4申诉中,9已完成"
      },
      OrderPage: [],
      timer: null,
      // 取消订单，反馈列表
      feedbackList: [],
      cancelDialogVisible: false,
      id: '',//订单id
    }
  },
  computed: {
    ...mapState(['locale', 'UserInfo'])
  },
  destroyed () {
    clearInterval(this.timer)
  },
  mounted () {
    this.$nextTick(() => {
      this.init()
    })
  },
  methods: {
    init () {
      this.GetOrderPageParams.page = 1
      this.GetOrderPage()
    },
    handleCurrentChange (val) {
      this.GetOrderPageParams.page = val
      this.$goPageTop('.el-scrollbar__wrap');
      this.GetOrderPage()
    },
    // 查询订单列表
    GetOrderPage (val) {
      clearInterval(this.timer)
      if(val){
        // val 为true：查询条件改变
        this.GetOrderPageParams.page=1
      }
      this.$api.OtcOrder.GetOrderPage(this.GetOrderPageParams).then(res => {
        if (res.isSuccess) {
          this.OrderPage = res.data.rows
          this.pageTotal = res.data.count
          // // 倒计时
          // this.timer = setInterval(() => {
          //   this.Order.countDown--
          //   if (this.Order.countDown <= 0) {
          //     clearInterval(this.timer)
          //     this.GetOrder()
          //   }
          // }, 1000)
          this.timer = setInterval(() => {
            let n = 0
            this.OrderPage.map(item => {
              if (item.countDown) {
                item.countDown--
              } else {
                n++
                if (this.OrderPage.length == n) {
                  clearInterval(this.timer)
                }
              }
            });
          }, 1000);
        }
      })
    },
    // $dateFrom.formatSeconds(item.countDown)
    handleClick (tab, event) {
      this.GetOrderPageParams.page = 1
      this.GetOrderPage()
    },
    // 取消订单
    CancelOrder () {
      let params = {
        "orderID": this.id,// "订单ID"
      }
      this.$api.OtcOrder.CancelOrder(params).then(res => {
        if (res.isSuccess) {
          this.cancelDialogVisible = false
          this.feedbackList = []
          this.$message.success("订单取消成功")
          this.GetOrderPage()
        }
      })
    },
  },
}
